<script>
import { register, sendVal } from "@/api/user";
import topbar from "@/components/Topbar";
import { isPsw, isUserName, isMobile } from "@/utils/validate.js";

export default {
  components: {
    topbar
  },
  data() {
    return {
      button: {
        content: "发送验证码",
        totalTime: 59,
        canClick: true
      },
      sliderVal: 0,
      dataForm: {
        name: "",
        password: "",
        checkPass: "",
        mobile: "",
        validateVal: "",
        uuid: ""
      },
      hasAgree: false,
      showAgreeDialog: false,
      loading: false,
      dialogFormVisible: false
    };
  },
  computed: {
    rules() {
      const checkName = (rule, value, callback) => {
        if (value === "") {
          return callback(new Error("用户名不能为空"));
        }
        if (!isUserName(value)) {
          return callback(new Error("用户名由5-25位的字母或汉字组成"));
        }
        callback();
      };

      const checkMobile = (rule, value, callback) => {
        if (value === "") {
          return callback(new Error("手机不能为空"));
        } else {
          if (isMobile(value)) {
            callback();
          } else {
            return callback(new Error("请输入有效的手机号"));
          }
        }
      };
      const validatePass = (rule, value, callback) => {
        if (value === "") {
          callback(new Error("请输入密码"));
        } else {
          if (!isPsw(value)) {
            callback(
              new Error("密码长度6-18位，数字、字母、字符至少包含其中两种")
            );
          }
          if (this.dataForm.checkPass !== "") {
            this.$refs.dataForm.validateField("checkPass");
          }
          callback();
        }
      };
      const validatePass2 = (rule, value, callback) => {
        if (value === "") {
          callback(new Error("请再次输入密码"));
        } else if (value !== this.dataForm.password) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      };
      const that = this;
      return {
        name: [{ validator: checkName, trigger: "change" }],
        password: [{ validator: validatePass, trigger: "change" }],
        checkPass: [{ validator: validatePass2, trigger: "change" }],
        mobile: [{ validator: checkMobile, trigger: "change" }],
        agree: [{ validator: that.checkAgree, trigger: "change" }]
      };
    }
  },
  methods: {
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "";
      } else {
        this.passwordType = "password";
      }
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    submitForm() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.loading = true;
          register(this.dataForm).then(response => {
            if (response.code === 0) {
              this.$message({
                message: "注册成功",
                type: "success"
              });
              this.$router.push({ path: this.redirect || "/login" });
              this.loading = false;
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
              this.loading = false;
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    validateSlider(val) {
      if (val === 100) {
        this.dialogFormVisible = true;
        this.sliderVal = 0;
        this.countDown();
      } else {
        this.sliderVal = 0;
      }
    },
    handleClose() {
      this.sliderVal = 0;
      this.dialogFormVisible = false;
    },
    countDown() {
      const mobileInputEl = this.$refs["dataForm"];
      if (!mobileInputEl) {
        return;
      }
      this.button.canClick = false;
      mobileInputEl.validateField("mobile", async error => {
        try {
          if (!error) {
            this.button.content = this.button.totalTime + "s后重新发送";
            let clock = window.setInterval(() => {
              this.button.totalTime--;
              this.button.content = this.button.totalTime + "s后重新发送";
              if (this.button.totalTime < 0) {
                window.clearInterval(clock);
                this.button.content = "重新发送";
                this.button.totalTime = 10;
                this.button.canClick = true; //这里重新开启
              }
            }, 1000);
            const res = await sendVal(this.dataForm.mobile);
            if (res.code !== 0) {
              window.clearInterval(clock);
              this.button.content = "重新发送";
              this.button.totalTime = 10;
              this.button.canClick = true;
              this.$message({
                message: res.message,
                type: "error"
              });
            } else {
              this.dataForm.uuid = res.data;
              this.$message({
                message: "验证码发送成功",
                type: "success"
              });
            }
          } else {
            this.button.canClick = true;
          }
        } catch (e) {
          this.$message({
            message: "验证码发送失败",
            type: "error"
          });
        }
      });
    },
    showAgreeContent() {
      this.showAgreeDialog = true;
    },
    handleAgreeSubmit() {
      this.showAgreeDialog = false;
      this.hasAgree = true;
    },
    checkAgree(rule, value, callback) {
      if (!this.hasAgree) {
        return callback(new Error("请阅读并统一服务条款和隐私政策"));
      }
      callback();
    }
  }
};
</script>

